___CSS_LOADER_EXPORT___.push([module.id, `* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

body.black {
  background-color: #222121;
}

main {
  display: grid;
  grid-template-columns: repeat(auto-fill, 300px);
  gap: 30px;
  flex: 2;
  justify-content: center;
  margin-top: 5rem;
}

header {
  background: #fff;
  display: flex;
  justify-content: space-between;
  padding: 1rem 1rem;
  box-shadow: 0 0 10px #0000001a;
  position: sticky;
  top: 0;
  z-index: 999;
}

.desktop-menu {
  display: flex;
  justify-content: center;
  align-items: center;
}

.logo {
  display: flex;
  width: auto;
  height: 50px;
}

.categories {
  display: flex;
  margin-top: 0.8rem;
  gap: 3rem;
}

.categories .navlink {
  list-style: none;
}

.categories .navlink a {
  color: black;
  text-decoration: none;
}

.navlink :hover {
  background-color: aqua;
}

h1 {
  background-color: burlywood;
}

/*
img {
  height: 400px;
} */

.movieImage {
  transition: all 0.3s ease-in-out;
  cursor: pointer;
}

.movieImage:hover {
  transform: scale(1.05);
}

.card {
  display: grid;
  place-items: center;
  border-radius: 8px;
  padding: 1rem 0;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease-in-out;
}

.comment-button {
  padding: 0.5rem 1rem;
  border-radius: 8px;
  border: none;
  cursor: pointer;
  font-size: 1rem;
  font-weight: 500;
  transition: all 0.3s ease-in-out;
}

.comment-button:hover {
  transform: scale(1.05);
}

.fa-heart {
  margin-top: 5px;
}

.commentLike-div {
  display: flex;
  width: fit-content;
  gap: 20px;
  margin-top: 5px;
}

footer {
  padding: 2rem;
  background-color: turquoise;
}

.active-popup {
  background-color: #222121;
  margin: 5% 15%;
  padding: 4rem 5% 4%;
  max-width: 700px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  position: relative;
  border: 2px solid rgb(163, 12, 12);
}

.active-popup * {
  color: white;
}

.active-popup #close-btn {
  all: unset;
  position: absolute;
  right: 1rem;
  top: 1rem;
}

.active-popup .info {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 1rem;
  width: 100%;
  max-width: 300px;
}

.active-popup .info p:nth-child(4) {
  width: 100%;
}

.active-popup > img {
  height: auto;
  width: 100%;
  max-width: 300px;
}

.active-popup button img {
  height: 2rem;
  width: 2rem;
}

.active-popup h2,
.active-popup .score {
  font-size: 1.5rem;
}

.info ul {
  list-style: none;
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;
  gap: 1rem;
  width: 100%;
}

.info ul li {
  background-color: #504e4e;
  padding: 0.4rem 1rem;
}

.info ul li:hover {
  background-color: #3b3838;
  cursor: pointer;
}

.hidden {
  display: none;
}

.comment-section {
  margin-top: 1rem;
  width: 80%;
}

.comment-section h2 {
  text-align: center;
}

.comment-section *::placeholder {
  font-family: monospace;
}

.add-comment input,
.add-comment textarea {
  padding: 0.3rem;
  color: #222121;
  height: 1.5rem;
}

.add-comment {
  margin: 1.5rem auto 0;
  width: 60%;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.add-comment textarea {
  height: 4rem;
}

.add-comment button {
  border: none;
  background-color: #504e4e;
  padding: 0.5rem;
}

.comment-section button:hover {
  background-color: #3b3838;
  cursor: pointer;
}

.comment-history {
  margin: 1rem 0 1.5rem;
  display: flex;
  flex-direction: column;
  gap: 0.8rem;
}

.single-comment {
  display: flex;
  flex-wrap: wrap;
  align-items: baseline;
  gap: 0.5rem;
  padding: 1rem;
  border: 1px solid #3b3838;
  border-radius: 1rem;
}

.single-comment span:nth-of-type(1) {
  color: #837e7e;
  width: 100%;
}

.single-comment span:nth-of-type(2) {
  font-size: 1.4rem;
}

.single-comment span:nth-of-type(3) {
  font-style: italic;
}

#name-error,
#comment-error {
  opacity: 0;
}

#name-error.show,
#comment-error.show {
  opacity: 1;
}

.like-count {
  display: block;
  margin-top: 5px;
  font-size: 12px;
  color: rgb(27, 72, 207);
}
